import React, { Component } from "react";
import Nav from "./nav";
import NavMenu from "./navMenu";
import Slide from "./slide/slide";
import BottomNavigation from "./bottomNavigation";
import DrawerLayout from "./drawerLayout";

class Home extends Component {
  state = {};
  render() {
    return (
      <div>
        <Nav />
        <NavMenu />
        <Slide />
        <BottomNavigation />
        <DrawerLayout />
      </div>
    );
  }
}

export default Home;
